$bg: #E7EBF0;

$size: 10px;

.modal-template {
    margin-top: 5vh;
    margin-right: 5vw;
    margin-left: 5vw;
    background-color: white;
    border-radius: 4px;

    .modal-header {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-end;
        padding-left: $size*2;
    }

    .modal-content {
        padding: $size*2;
        max-height: 90vh;
        overflow: auto;

        .carousel {
            .image {
                border-radius: 4px;
                display: flex;
                flex-direction: column;
                align-items: center;

                img {
                    max-height: 50vh;
                    width: 100%;
                    object-fit: contain;
                }
            }
        }
    }
}

.root {
    background-image: url("../img/bg.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.app {
    height: 100vh;
    overflow: auto;
    scroll-behavior: smooth;

    .nav {
        background-color: #ffffff88;
        position: absolute;
        width: 100vw;
        height: 5vh;
        z-index: 1;
        display: flex;
        justify-content: flex-end;
        align-items: center;

        ul {
            display: flex;
            flex-direction: row;
            margin-right: $size*3;

            li {
                list-style: none;
                margin-right: $size*3;

                a {
                    text-decoration: none;
                    color: rgb(49, 49, 49);
                    font-weight: bold;
                }

                a:hover {
                    text-decoration: underline;
                }
            }
        }

        .burger {
            visibility: hidden;
            margin-right: $size;
        }

        @media only screen and (max-width: 600px) {
            ul {
                visibility: hidden;
            }

            .burger {
                visibility: visible;
            }
        }
    }

    .home {
        display: flex;
        flex-direction: column;
        align-items: center;
        min-height: 100vh;

        .avatar-container {
            margin-top: 12vh;

            .avatar {
                width: 250px;
                height: 250px;
            }
        }

        .text-container {
            margin-top: 8vh;
            max-width: 1000px;
            display: flex;
            flex-direction: column;
            align-items: center;
            background-color: $bg;
            padding: $size*2;
            border-radius: 4px;

            .name-container {
                margin-bottom: $size;
                text-align: center;

                span {
                    font-size: 3rem
                }
            }

            .moto-container {
                margin-bottom: $size;

                span {
                    font-size: 1.5rem
                }
            }

            .skill-container {
                text-align: center;

                .skill {
                    display: inline;
                }
            }
        }
    }

    .bio {
        display: flex;
        flex-direction: column;
        align-items: center;
        min-height: 100vh;

        .text-container {
            margin-top: 20vh;
            margin-bottom: 20vh;
            width: 350px;
            display: flex;
            flex-direction: column;
            align-items: center;
            background-color: $bg;
            padding: $size*2;
            border-radius: 4px;

            .field:not(:last-child) {
                margin-bottom: $size*2;
            }

            .field {
                width: 100%;
            }
        }
    }

    .project {
        padding: $size*2;
        padding-top: 6vh;
        min-height: 100vh;
        background-color: $bg;

        .card-content {
            padding-bottom: 0;
        }

        .card-footer {
            display: flex;
            flex-direction: column;
            align-items: flex-end;
        }
    }

    .scholl {
        display: flex;
        flex-direction: column;
        align-items: center;
        min-height: 100vh;

        .container {
            margin-top: 25vh;
            max-width: 1000px;
            background-color: $bg;
            padding: $size*2;
            border-radius: 4px;
        }
    }

    .contact {
        display: flex;
        flex-direction: column;
        align-items: center;
        min-height: 100vh;

        .container {
            margin-top: 40vh;
            max-width: 1000px;
            display: flex;
            flex-direction: column;
            align-items: center;
            background-color: $bg;
            padding: $size*2;
            border-radius: 4px;

            a:not(:last-child) {
                margin-bottom: $size;
            }

            a {
                text-decoration: none;

                button {
                    width: 20vh;
                }
            }
        }
    }
}